body {
  background: black;
}

.App {
  display: flex;
  flex-wrap: wrap;
  height: calc(100vh - 30px);
  margin-top: 30px;
  justify-content: space-evenly;
}

.users {
  display: flex;
    flex-wrap: wrap;
    margin-top: 30px;
    justify-content: space-evenly;
}

.updateContainer {
  position: fixed;
  background-color: darkgray;
  width: 80%;
  min-height: 50px;
  display: flex;
  bottom: 0;
  left: 10%;
  display: none;
}

.show {
  display: flex;
}

div.updateText {
  margin: auto;
  text-align: center;
}

button.updateButton {
  background: none;
  border: none;
  color: blue;
  font-size: 16px;
  font-weight: 600;
  margin-left: 5px;
}

.user-card-wrapper {
  display: flex;
  margin: auto 20px;
}

.user-card {
  height: 250px;
  width: 200px;
  border-radius: 20px;
  background: white;
  cursor: pointer;
  margin-bottom: 15px;
  transition: transform 0.15s linear;
}

.user-card:hover {
  transform: translateY(-10px);
  transition: transform 0.15s linear;
}

.user-card-avatar {
  text-align: center;
}

.user-avatar {
  border-radius: 50%;
  margin-top: 25px;
}

.user-card-email {
  text-align: center;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
}

.user-card-name {
  text-align: center;
  text-align: center;
  font-size: 20px;
  font-weight: 500;
}
